.cursor {
  height: 62px;
  width: 62px;
  border: 1px solid white;
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: 500ms ease-out;
  z-index: 2;
  backdrop-filter: brightness(115%);

  &.hovered {
    height: 35px;
    width: 35px;
  }
}
