.form-component {
  min-height: 200px;
  min-width: 200px;
  display: flex;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 30px;

  #search-input,
  .submit {
    font-family: "Raleway", sans-serif;
    height: 30px;
    min-width: 200px;
    padding: auto 10px;
    text-align: center;
    border: none;
  }
  .submit {
    background: blue;
    color: white;
    border-radius: 0 0 10px 10px;
    cursor: pointer;
  }
  #search-input {
    color: black;
    border-radius: 10px 10px 0 0;
  }
}

.result {
  position: absolute;
  top: 250px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
}
.btn-sort-container {
  background: blue;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 230px;
  position: absolute;
  left: 594px;
  border-radius: 10px;

  .btn-sort {
    cursor: pointer;
  }
}
