.navigation {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 1.3rem;

  ul {
    text-decoration: none;
    display: flex;
  }
  li {
    list-style: none;
    padding: 10px;
    margin-right: 10px;
  }
}

.nav-active {
  li {
    background-color: white;
    color: black;
    border-radius: 20px;
  }
}
