.card {
  min-height: 400px;
  width: 750px;
  border: 2px solid white;
  border-radius: 10px;
  margin: 10px;
  display: grid;
  grid-template-columns: 33% 67%;
  position: relative;

  .left-grid {
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      "i1"
      "i2"
      "i3";
  }
  .note {
    padding: 0 0 5px 0;
    text-align: center;
  }
  img {
    width: 80%;
    margin: 10px;
    border-radius: 10px;
  }
  .btn-cdc {
    background: blue;
    color: white;
    position: absolute;
    bottom: 16px;
    left: 85%;
    transform: translateX(-50%);
    padding: 14px 0;
    width: 150px;
    text-align: center;
    border-radius: 15px;
    cursor: pointer;
    transition: 0.2s;
    z-index: 50;

    &:hover {
      background: lightblue;
      box-shadow: 0 0 10px #546ee4d2;
      font-size: 1.025rem;
    }
  }
  h1 {
    text-align: center;
    margin: 10px;
  }
  h5 {
    font-size: 1rem;
    text-align: center;
    padding: 0 0 10px 0;
  }
  h3 {
    padding-bottom: 5px;
    font-size: 1.2rem;
  }
  p {
    font-family: "Raleway", sans-serif;
    font-size: 0.95 rem;
  }
  ul {
    position: absolute;
    bottom: 10px;
    display: flex;
    width: 300px;
    height: 75px;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: space-around;
    justify-content: space-evenly;

    li {
      list-style-type: none;
      padding: 8px;
      background: white;
      color: black;
      border-radius: 30px;
      opacity: 0.7;
    }
  }
}
